*{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body{
    background: #90caf9;
}

.card{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #f4f4f4;
    width: 40%;
    margin: auto;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
}

.group{
    position: relative;
    margin-bottom: 45px; 
}

.input{
    background: #f4f4f4;
    padding: 20px;
    border: none;
    display: block;
    border-bottom: 1px solid #757575;
}

input:focus{
    outline:none; 
}

input:hover{
    border-bottom: 2px solid #757575;
}

label{
    position: relative;
    left: 5px;
    top: 10px;
    font-size: 18px;
    font-weight: normal;
    position: absolute;
    transition:0.2s ease all; 
    color:#999;
}

input:focus ~ label, input:valid ~ label{
    top:-20px;
    font-size:14px;
    color:#757575;
}

.btn {
    position: relative;
    display: block;
    margin: 30px auto;
    padding: 0;
    overflow: hidden;
    border-width: 0;
    outline: none;
    border-radius: 2px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    background: #1565c0;
    color: #ecf0f1;
    transition: background-color .3s;
}

.btn:hover, .btn:focus {
    background: #5e92f3;
}

.btn > * {
    position: relative;
}
  
.btn span {
    display: block;
    padding: 12px 24px;
}
  
.btn:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    width: 0;
    padding-top: 0;
    border-radius: 100%;
    background-color: rgba(236, 240, 241, .3);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
  
.btn:active:before {
    width: 120%;
    padding-top: 120%;
    transition: width .2s ease-out, padding-top .2s ease-out;
}
  